import {action, observable} from 'mobx-angular';
import {Injectable} from '@angular/core';
import {IVoucherRequest} from "../model/voucher.model";
import {ISold} from "../model/sold.model";

@Injectable({providedIn: 'root'})
export default class SoldStore {
  @observable sold: ISold[] = [];

  constructor() {
    this.sold = [];
  }

  @action
  async setSold(sold: ISold[]){
    console.log('Setting sold requests: ', sold);
    this.sold.push(...sold);
  }

  @action
  addSoldRequest(sold: ISold){
    console.log('Setting sold request: ', sold);
    switch(sold.approved) {
      case 'TO_BE_APPROVED':
        sold.approved = 'Nije jo≈° odobreno';
        // code block
        break;
      case 'APPROVED':
        sold.approved = 'Odobreno';
        // code block
        break;
      default:
        sold.approved = 'Nije odobreno';
      // code block
    }
    this.sold.unshift(sold);
  }




}
